import React, { useEffect, useRef } from "react";
import Design, { Fonts } from "../design/design";
import { TextV3, Lang } from "../TypesV3";
import { HTMLNodeV3 } from "./Card";
import Color from "../../v2/preference/Color";
import store from "../reducers";


export interface DisplayCardProps {
    style?: React.CSSProperties;
    title: TextV3;
};

export const DisplayCard: React.FC<DisplayCardProps> = props => {
    const lang: Lang = store.getState().LangRedux.lang;

    const ref = useRef<HTMLDivElement>() as React.RefObject<HTMLDivElement>;

    useEffect(() => {
        // componentDidMount
        // console.log(ref);
    });

    return (
        <div className="DisplayCardContainer" >
            <div className="card DisplayCard" ref={ ref } style={{
                color: Design.gold,
                boxShadow: `6px 5px 0 1px ${ Design.black.replace("(", "a(").replace(")", ",0.33)") }`,
                ...props.style
            }} >
                <div className="DisplayCardContent" >
                    <header
                    style={{
                        fontFamily: Fonts[lang] || "inherit"
                    }} >
                        <span
                        style={{
                            borderBottom: `1px solid ${ Design.gold }`,
                            textShadow: `1.6px 1.2px 2px ${ Color.setLightness(Design.gold, 0.33) }`
                        }} >
                            <HTMLNodeV3>
                                { props.title }
                            </HTMLNodeV3>
                        </span>
                    </header>
                    { props.children }
                </div>
            </div>
        </div>
    );
};
